.get-started-content-wapper{
    background: linear-gradient(180deg, rgba(6, 16, 21, 0) 0%, #0B1D26 100.38%);
}

.heading-lines-get{
    width:50px;
    background: #FBD784;
    height:1px;
    position: absolute;
   margin-top:3%;
  
}
.get-started-heading-holder{
padding-left:15%;
padding-top:10%;


   
}
.get-started-heading{
    display: flex;
    align-items: stretch;
    gap:12%;
    

}
.page-number{
    font-size: 6em;
    color: #FFFFFF;
    z-index: 1;
    opacity: 0.1;
    position: absolute;
  top:3%;
  left:5%;
  font-weight:700;
}
.get-started-content{
  
}
.img-holder img{

    width:60%; 
    padding: 10px;
    padding-top:10%;
}
.Learn-More{
    color: #FBD784;

}
.get-started-content-wapper{
    position: relative;
}

@media only screen and (min-width: 1400px) {
    .page-number{
       
      top:8%;
    
    }
    .get-started-heading-holder{
        padding-left:10%;
        padding-top:10%;
        
        }
}
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .get-started-content-wapper{
       font-size:0.7em;
    }
    .page-number{   
        top:1%;
      
      }
      .img-holder img{
        width:90%;
        margin-left:5%;
      }
    
}
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width:600px){
    .get-started-content-wapper{
        font-size:0.9em;
     
     }
     .page-number{   
        top:0%;
      
      }
      .img-holder img{
        width:90%;
        margin-left:5%;
      }
      .get-started-headin{
        margin-left: 15px;
      }
}
/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .get-started-headin{
    margin-left: 20px;
   
  }
  .heading-lines-get{
    width:40px;   
   margin-top:0.9%;
   left:11%;
  
}
.page-number{   
  top:1%;
  margin-left:3%;

}
}


